import {CommonModule} from '@angular/common';
import {NgModule, ModuleWithProviders} from '@angular/core';
import {FormsModule} from '@angular/forms';
import {
  AdaptUploadItemModule,
  AdaptTruncateFileNameModule,
  AdaptTooltipModule,
  AdaptAlertModule,
  AdaptBusyModule,
  AdaptButtonModule,
  AdaptCollapseModule,
  AdaptFileSizePipeModule,
  AdaptFileTypesPipeModule,
  AdaptEmptyStateModule,
  AdaptIconModule,
  AdaptPopoverModule,
  AdaptUploaderModuleConfig,
  SendFileService,
  AdaptRxLabelModule
} from '@bmc-ux/adapt-angular';

import {AdaptUploaderComponent} from './uploader.component';
import {AdaptSingleUploaderComponent} from './single-uploader/single-uploader.component';


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    AdaptButtonModule,
    AdaptAlertModule.forRoot(),
    AdaptCollapseModule,
    AdaptTooltipModule,
    AdaptPopoverModule.forRoot(),
    AdaptIconModule,
    AdaptBusyModule,
    AdaptTruncateFileNameModule,
    AdaptUploadItemModule,
    AdaptFileSizePipeModule,
    AdaptFileTypesPipeModule,
    AdaptEmptyStateModule,
    AdaptRxLabelModule
  ],
  declarations: [
    AdaptUploaderComponent,
    AdaptSingleUploaderComponent
  ],
  exports: [AdaptUploaderComponent, AdaptSingleUploaderComponent]
})
export class AdaptUploadModule {
  static forRoot(config?: AdaptUploaderModuleConfig): ModuleWithProviders<AdaptUploadModule> {
    return {
      ngModule: AdaptUploadModule,
      providers: [
        (config && config.uploader) ? config.uploader : {provide: 'UploaderService', useClass: SendFileService},
        {provide: SendFileService, useClass: SendFileService}
      ]
    };
  }
}

export * from './uploader.component';
export * from './single-uploader/single-uploader.component';
